import React from 'react';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import cssVar from 'views/mobileView/styles';
import Tabs from 'views/mobileView/components/atoms/Tabs';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';
import MintboxIcon from 'views/mobileView/components/atoms/MintboxIcon';
import CurrencyInput from 'react-currency-input-field';
import executeSmallCaseTransaction from './ExecuteOrder';
import AdminServices from 'services/api/AdminServices';
import { toast } from 'react-toastify';
import AMCIcon from 'views/mobileView/components/atoms/AMCIcon';
import { CurrencyFormat } from 'views/mobileView/components/atoms/CurrencyFormat';
export default function RedeemMintbox(props) {
    const [loading, setLoading] = React.useState(false);
    const FUNDDATA = props.location.state.fundData
    const CONSTITUENTS = props.location.state.constituents

    React.useEffect(() => {
        console.log(FUNDDATA)
        console.log(CONSTITUENTS)
    }, [])

    const RedeemMintbox = () => {
        const [amount, setAmount] = React.useState();
        const [isChecked, setIsChecked] = React.useState(false);

        React.useEffect(() => {
            if (isChecked) {
                setAmount(FUNDDATA.MintboxBalance)
            }
        }, [isChecked])

        const getFundQunatities = async (etfs, constituents, amount) => {
            var etfsData = await AdminServices.getETFsById(etfs.toString()).then(res => res)
            console.log(etfsData)
            if (etfsData.type) {
                console.log(etfsData)
                const weight = {};
                constituents.map((item) => {
                    weight[item.FundCode] = item.CurrentWeightage / 100
                })
                const currentNav = {};
                etfsData.data.map(item => {
                    currentNav[item.Symbol] = item.NAV
                })
                var dataFinal = {
                    "weights": weight,
                    "currentNav": currentNav,
                    "investment": amount
                }
                console.log(dataFinal)
                var finalDATA = await AdminServices.getETFQuantities(JSON.stringify(dataFinal)).then(res => res)
                console.log(finalDATA)
                if (finalDATA.type) {
                    var quant = finalDATA.data.Result
                    quant = JSON.parse(quant).data
                    console.log(finalDATA)
                    console.log(quant)
                    return quant
                } else {
                    toast.error('Error. Increase redemption amount')
                    return {}
                }
            } else {
                toast.error('Error')
                return {}

            }

        }

        const createOrder = async () => {
            setLoading(true)
            let securities = []
            if (isChecked) {
                securities = CONSTITUENTS.map((e) => ({ "ticker": e.FundCode, "quantity": e.Units + "", type: "SELL" }));
            } else {
                var etfs = CONSTITUENTS.map(etf => etf.FundCode)
                var orderQuant = await getFundQunatities(etfs, CONSTITUENTS, amount)
                if(Object.keys(orderQuant).length){
                    securities = Object.entries(orderQuant).map((e) => ({ "ticker": e[0], "quantity": e[1] + "", type: "SELL" }))
                }
            }
            console.log(securities)
            if (securities.length) {
                await executeSmallCaseTransaction({
                    navigation: props.history,
                    securities: securities,
                    amount: Number(amount),
                    isSIP: false,
                    isUnitsBased: false,
                    units: 0,
                    sipDate: 0,
                    SCREENTYPE: 3,
                    FUNDDATA: FUNDDATA,
                    isInvest: false
                })
            } else {
                toast.error('Increase redemption amount')
            }
            setLoading(false)
        }

        const onRedeemClick = () => {
            if (amount && amount !== 0) {
                if (amount > FUNDDATA.MintboxBalance) {
                    toast.error(`You can redeem maximum ${CurrencyFormat(FUNDDATA.MintboxBalance, 'long', 2)}`)
                } else {
                    createOrder()
                }
            }
        }
        return (
            <>
                <div style={{ margin: '4%' }}>
                    <div className='header1dark' style={{ paddingTop: '4%' }}>
                        Redemption amount
                    </div>
                    <div className='smallTextLight'>
                        Enter the amount to redeem
                    </div>
                    <CurrencyInput
                        className='normalInput rupee'
                        id="amount"
                        name="amount"
                        placeholder="10,000"
                        value={amount}
                        allowDecimals={false}
                        allowNegativeValue={false}
                        disableAbbreviations={false}
                        prefix=' '
                        onValueChange={(value) => { setIsChecked(false); setAmount(value) }}
                        intlConfig={{ locale: 'en-IN', currency: 'INR' }}
                    />
                    <div style={{ marginTop: "4%", display: "flex" }}>
                        <input
                            type="checkbox"
                            id="checkbox"
                            name="checkbox"
                            checked={isChecked}
                            onChange={() => setIsChecked(!isChecked)}
                            className='greenCheckbox'
                        />
                        <label htmlFor="checkbox" style={{ flex: 1 }}>
                            Fully redeem this minbox
                        </label>
                    </div>
                </div>
                <div className='bodyTextLight' style={{ position: "fixed", bottom: 70, width: "100%", padding: "2% 4%", borderTop: "1px solid " + cssVar.getPropertyValue('--light') }}>
                    Note: The actual amount redeemed may be slightly different due to constantly changing prices
                </div>
                <div className='footer'>
                    <button
                        className='outlineButton'
                        style={{ color: cssVar.getPropertyValue('--red'), borderColor: cssVar.getPropertyValue('--red') }}
                        onClick={() => onRedeemClick()}
                    >
                        Confirm and redeem
                    </button>
                </div>
            </>
        )
    }

    const RedeemConstituents = () => {
        return (
            <>
                <div>
                    <div className='header1dark' style={{ padding: '4%', marginTop: '4%' }}>
                        Choose constituent to redeem
                    </div>
                    {
                        CONSTITUENTS.map(e => {
                            return (
                                <div style={{ padding: '4%', display: 'flex', gap: '4%', borderBottom: '1px solid ' + cssVar.getPropertyValue('--light') }}>
                                    <AMCIcon amcName={e.AMCName} width='40px' height='40px' />
                                    <div style={{ flex: 1 }}>
                                        <div className='header2dark' style={{ marginBottom: '2%' }}>
                                            {e.ETFName}
                                        </div>
                                        <div className='smallTextLight'>Balance <span className='smallTextDark'>{e.MintboxETFBalance}</span> | Units <span>{e.Units}</span></div>
                                    </div>
                                    <button className='outlineButton' style={{ margin: 'auto', width: 'auto', color: cssVar.getPropertyValue('--red'), borderColor: cssVar.getPropertyValue('--red'), height: '33px', padding: '0 3%' }}
                                        onClick={() => props.history.push({ pathname: '/portfolio/redeem-etf', state: { fundData: e } })}>
                                        Redeem
                                    </button>
                                </div>
                            )
                        })
                    }
                </div>
            </>
        )
    }

    const keyTitles = [
        { title: "Redeem mintbox (recommended)", view: RedeemMintbox() },
        { title: "Redeem constituents", view: RedeemConstituents() },
    ];

    if (loading) {
        return (
            <div style={{ flex: 1 }}>
                <MainHeader
                    header="Redeem"
                    navigation={props.history}
                />
                <ActivityLoaderMintbox />
            </div>
        );
    }
    else return (
        <div className='mainContainer'>
            <MainHeader
                navigation={props.history}
                header="Redeem"
            />
            <div className="dataContainer px-0">
                <div style={{ display: 'flex', alignItems: "center", gap: '4%', padding: '4%' }}>
                    <MintboxIcon risk={FUNDDATA.RiskLevel} height="40px" width="40px" />
                    <div style={{ flex: 1 }}>
                        <div className='header1dark'>
                            {FUNDDATA.MintboxName}
                        </div>
                    </div>
                </div>
                <Tabs tabsObject={keyTitles} color={cssVar.getPropertyValue('--darkGreen')} />
            </div>

        </div>
    );
}

